import React, { useState } from 'react';


import { LAMPORTS_PER_SOL, } from '@solana/web3.js'
import { shortenAddress } from '../utils/shortenAddress';
import Svg from 'react-inlinesvg'

import '../css/ActiveGames.css'
import '../css/Coin.css'
import '../css/Modal.css'
import Game from '../interfaces/Game';
import { Link } from 'react-router-dom';
import moment from 'moment'
import '../css/RecentGames.css'
import { displayName } from '../utils/displayName';
import { useSelector } from 'react-redux';
import { CombinedReducer } from '../store';
import axios from 'axios';
import { useEffect } from 'react';

interface Props {
    games: Game[]
}

function RecentGames() {
    const [recentGames, setRecentGames] = useState<Game[]>([])
    const games = useSelector<CombinedReducer, Game[]>((state) => state.games)

    const loadRecentGames = async () => {
        const recentGames = (await axios.get('/api/game/recentGames')).data
        setRecentGames(recentGames)
    }

    useEffect(() => {
        loadRecentGames()
    }, [])

    return (
        <div className='recentGamesWrapper box'>
            <h2 className='recentGamesHeader'>Recent Plays</h2>
            <div className='recentGames'>
                {games.map(game => {
                    if (game.status !== 'removed' && game.status !== 'revealed' || !game.opponent) return

                    if (game.winner) {
                        const loser = game.creator._id === game.winner._id ? game.opponent : game.creator
                        return <div key={game._id} className='recentGame'>
                            <Link className='profileLink' to={`/u/${game.winner.publicKey}`}>{shortenAddress(game.winner.publicKey)}</Link> just <span className='win'>won</span> <span>{game.amount * 2 / LAMPORTS_PER_SOL}</span> SOL against <Link className='profileLink' to={`/u/${loser.publicKey}`}>{shortenAddress(loser.publicKey)}</Link>
                        </div>
                    } else {
                        return <div key={game._id} className='recentGame'>
                            <Link className='profileLink' to={`/u/${game.creator.publicKey}`}>{shortenAddress(game.creator.publicKey)}</Link> had a <span className='draw'>draw</span> with <Link className='profileLink' to={`/u/${game.opponent.publicKey}`}>{shortenAddress(game.opponent.publicKey)}</Link>
                        </div>
                    }
                })}
                {recentGames.map(game => {
                    if (!game.opponent) return

                    if (game.winner) {
                        const loser = game.creator._id === game.winner._id ? game.opponent : game.creator
                        return <div className='recentGame'>
                            <Link className='profileLink' to={`/u/${game.winner.publicKey}`}>{shortenAddress(game.winner.publicKey)}</Link> <span className='win'>won</span> <span>{game.amount * 2 / LAMPORTS_PER_SOL}</span> SOL against <Link className='profileLink' to={`/u/${loser.publicKey}`}>{shortenAddress(loser.publicKey)}</Link>
                        </div>
                    } else {
                        return <div className='recentGame'>
                            <Link className='profileLink' to={`/u/${game.creator.publicKey}`}>{shortenAddress(game.creator.publicKey)}</Link> had a <span className='draw'>draw</span> with <Link className='profileLink' to={`/u/${game.opponent.publicKey}`}>{shortenAddress(game.opponent.publicKey)}</Link>
                        </div>
                    }
                })}
            </div>
        </div>
    );
}

export default RecentGames;