import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import axios from 'axios';

import Svg from 'react-inlinesvg'

import '../css/Profile.css'
import '../css/Coin.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import Game from '../interfaces/Game';
import { createAvatar } from '@dicebear/avatars'
import * as avatarStyle from '@dicebear/adventurer';
import UserGames from './UserGames';
import { toast } from 'react-toastify';
import { useDispatch, useSelector } from 'react-redux';
import { CombinedReducer } from '../store';

function MyProfile() {
    const [games, setGames] = useState<Game[]>([])
    const [gamesWon, setGamesWon] = useState(0)
    const [gamesLost, setGamesLost] = useState(0)
    const [user, setUser] = useState<User>()
    const [editData, setEditData] = useState({
        username: ''
    })
    const [editUserModalOpen, setEditUserModalOpen] = useState(false)
    const dispatch = useDispatch()

    const publicKey = useSelector<CombinedReducer, string>((state) => state.user?.publicKey)

    const editUser = async () => {
        try {
            const newUser = (await axios.post(`/api/u/editUser`, editData)).data
            console.log({ newUser })

            setUser(user => newUser)
            dispatch({ type: 'LOAD_USER', payload: user })

            toast.success('Profile has been updated')
        } catch (e: any) {
            toast.error(e.response.data.message.toString())
        }
    }

    useEffect(() => {
        if (!publicKey) return

        const getGames = async () => {
            const user: User = (await axios.get(`/api/u/${publicKey}`)).data
            const games: Game[] = (await axios.get(`/api/game/u/${publicKey}`)).data

            setEditData({
                username: user.username || ''
            })
            setUser(user)
            setGames(games)
        }

        getGames()
    }, [publicKey])

    useEffect(() => {
        if (!games || !user) return

        let gamesWon = 0
        let gamesLost = 0
        games.forEach(game => {
            if (game.status !== 'ended' || !game.winner) return

            if (game.winner._id === user._id) {
                gamesWon++
            } else {
                gamesLost++
            }
        })

        setGamesWon(gamesWon)
        setGamesLost(gamesLost)
    }, [user, games])

    return (
        <>
            {user ?
                <div className='Page Profile'>
                    <div className='profileBox'>
                        <div className='leftInfo'>
                            <Svg className='avatar' src={createAvatar(avatarStyle, { seed: user.publicKey.toString() })} />
                            <button onClick={() => setEditUserModalOpen(true)} className='editB'>Edit Profile</button>
                        </div>
                        <div className='rightInfo'>
                            {user.username ? <div className='username'>
                                <h1 className='value'>{user.username}</h1>
                            </div> : null}
                            <div className='address'>
                                <h2 className='value'>{user.publicKey}</h2>
                                <a className='scanLink' target="_blank" rel="noopener noreferrer" href={`https://solscan.io/account/${user.publicKey.toString()}`}><img src='/img/solscan.png' /></a>
                            </div>
                            <div className='gameStats'>
                                <div className='gameStat'>
                                    <h2>Games Won:</h2>
                                    <h2>{gamesWon}</h2>
                                </div>
                                <div className='statCircle' style={{
                                    backgroundImage: games.length > 0 ? `linear-gradient(#1C1D25, #1C1D25), linear-gradient( 45deg, rgba(37, 185, 0, 0.85), rgba(37, 185, 0, 0.85) ${gamesWon / (gamesWon + gamesLost) * 100}%, rgba(185, 37, 0, 0.85) ${gamesLost / (gamesWon + gamesLost) * 100}% )` : `linear-gradient(#1c1d25, #1c1d25), linear-gradient( 45deg, gray, gray 100% )`
                                }}>
                                </div>
                                <div className='gameStat'>
                                    <h2>Games Lost:</h2>
                                    <h2>{gamesLost}</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    {games.length > 0
                        ? <UserGames user={user} games={games} />
                        :
                        null}
                </div>
                : null
            }
            <div className='modal' style={{ display: editUserModalOpen ? 'flex' : 'none' }}>
                <div className='container'>
                    <div className='wrapper'>
                        <button className='closeB' onClick={() => setEditUserModalOpen(false)}><svg width="14" height="14"><path d="M14 12.461 8.3 6.772l5.234-5.233L12.006 0 6.772 5.234 1.54 0 0 1.539l5.234 5.233L0 12.006l1.539 1.528L6.772 8.3l5.69 5.7L14 12.461z"></path></svg></button>
                        <div className='logo'>
                            <img alt='logo' src='/logo.png' />
                        </div>
                        <h1 className='title'>Edit Profile</h1>
                        <div className='fields'>
                            <input value={editData.username} onInput={(event) => setEditData(data => { return { ...data, username: (event.target as HTMLInputElement).value } })} placeholder='username' />
                        </div>
                        <button onClick={() => editUser()} className='modalB'>Save</button>
                    </div>
                </div>
                <div className='overlay'></div>
            </div>
        </>
    );
}

export default MyProfile;