import React, { useState } from 'react';

import { LAMPORTS_PER_SOL } from '@solana/web3.js'
import Svg from 'react-inlinesvg'

import '../css/UserGames.css'
import '../css/Coin.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import Game from '../interfaces/Game';
import moment from 'moment'
import { Link } from 'react-router-dom';
import { displayName } from '../utils/displayName';
import { shortenAddress } from '../utils/shortenAddress';

interface Props {
    games: Game[],
    user: User
}


function UserGames(props: Props) {
    const calculateWinAmount = (amount: number, fee: number) => Math.round(amount * 2 * ((100 - fee) / 100)) / LAMPORTS_PER_SOL

    return (
        <>
            <table className='UserGames'>
                <thead>
                    <tr>
                        <th>Date</th>
                        <th>Bet</th>
                        <th>Move</th>
                        <th>Opponent</th>
                        <th>Opponent Move</th>
                        <th>Win Amount</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        props.games.map((game, index) => {
                            if (game.status !== 'ended' || !game.opponent) return

                            const move = game.creator._id === props.user._id ? game.creatorMove : game.opponentMove
                            const opponent = game.creator._id === props.user._id ? game.opponent : game.creator
                            const opponentMove = game.creator._id === props.user._id ? game.opponentMove : game.creatorMove
                            const winAmount = game.winner ? game.winner._id === props.user._id ? game.amount * 2 / LAMPORTS_PER_SOL : -game.amount / LAMPORTS_PER_SOL : 0

                            return <tr key={index} className='game' >
                                <td className='date'>{moment(game.updatedAt).format('MMM DD, h:mm A')}</td>
                                <td>{game.amount / LAMPORTS_PER_SOL} SOL</td>
                                <td className='move flip'><Svg src={`/img/move${move}.svg`} /></td>
                                <td><Link className='profileLink' to={`/u/${opponent.publicKey}`}>{shortenAddress(opponent?.publicKey)}</Link></td>
                                <td className='move'><Svg src={`/img/move${opponentMove}.svg`} /></td>
                                <td><span className={winAmount === 0 ? '' : winAmount > 0 ? 'win' : 'lose'}>{winAmount} SOL</span></td>
                            </tr>
                        }
                        )
                    }
                </tbody>
            </table>
        </>
    );
}

export default UserGames;