import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import axios from 'axios';

import Svg from 'react-inlinesvg'

import '../css/Profile.css'
import '../css/Coin.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import Game from '../interfaces/Game';
import UserGames from './UserGames';

function Profile() {
    const { publicKey } = useParams()
    const [user, setUser] = useState<User>()
    const [games, setGames] = useState<Game[]>([])
    const [notFound, setNotFound] = useState(false)
    const [gamesWon, setGamesWon] = useState(0)
    const [gamesLost, setGamesLost] = useState(0)

    const getUser = async (publicKey: string) => {
        const [responseUser, responseGame] = await Promise.all([axios.get(`/api/u/${publicKey}`), axios.get(`/api/game/u/${publicKey}`)])
        const [user, games] = [responseUser.data, responseGame.data]

        if (!user) setNotFound(true)

        setUser(user)
        setGames(games)
    }

    useEffect(() => {
        setUser(undefined)
        if (!publicKey) return

        getUser(publicKey)
    }, [publicKey])

    useEffect(() => {
        if (!games || !user) return

        let gamesWon = 0
        let gamesLost = 0
        games.forEach(game => {
            if (game.status !== 'ended' || !game.winner) return

            if (game.winner._id === user._id) {
                gamesWon++
            } else {
                gamesLost++
            }
        })

        setGamesWon(gamesWon)
        setGamesLost(gamesLost)
    }, [user, games])

    return (
        <div className='Page Profile'>
            {user ? <><div className='box profileBox'>
                <img className='avatar' src='/img/masterchip.png' />
                <div className='rightInfo'>
                    <div className='address'>
                        <h2 className='publicKey'>{user.publicKey}</h2>
                        {/* <a className='scanLink' target="_blank" rel="noopener noreferrer" href={`https://solscan.io/account/${user.publicKey.toString()}`}><img src='/img/solscan.png' /></a> */}
                    </div>
                    <div className='gameStats'>
                        <div className='gameStat'>
                            <h2 className='counter'>{gamesWon}</h2>
                            <h2 className='tag'>Won</h2>
                        </div>
                        <div className='statSeparator'>
                            /
                        </div>
                        <div className='gameStat'>
                            <h2 className='counter'>{gamesLost}</h2>
                            <h2 className='tag'>Lost</h2>
                        </div>
                    </div>
                </div>
            </div>
                <UserGames user={user} games={games} /></>
                : !notFound ? <h1>Loading user...</h1> : <h1>User was not found</h1>}
        </div>
    );
}

export default Profile;